            debugger
       var vm = this; 
     if (vm.selectedMembers.length > 2) {
      vm.type = 'web'
     if (vm.mobileType) {
        vm.type = "mobile";
    }
    
    let obj = {
        companyId: vm.sender.companyId,
        id: vm.sender.id,
        emaiId: vm.sender.emaiId,
        name: vm.sender.name,
        imgPath: vm.sender.getBlobUserUrl,
