    params.room = roomdata.replace(/,/g, '_'), 
    params.alias = vm.chatRoom.GROUP_ID
    params.for =  "update"
    params.groupName=vm.chatRoom.GROUP_NAME
    vm.$socket.emit('createRoom', params);
    this.IsgroupMemeber=false;
   //vm.$parent.closeGroup()
   //vm.groupShowProps = false
    vm.Grouptitle = ''
    //for login user select
    //vm.selectedMembers = []
    vm.selectedMembers.push({
        "USER_ID": vm.GetUserInfo.ID,
        "CONTACTNAME": vm.GetUserInfo.Name
    })
    vm.groupId = ''
} else {
    vm.ShowAlert(vm.$t("Atleast3Members"), "failure", true, vm.$t("Alert"));
}
// } else {
//     vm.ShowAlert(vm.$t("GroupChatName"), "failure", true, vm.$t("Alert"));
// }
},

        getUserlist(data) {
            this.selectedMembers = []
        if (data) {     
        data.forEach(element => {
          if (!this.selectedMembers.includes(element.useR_ID)) {
            this.selectedMembers.push(element.useR_ID);
          }
        });
        console.log(this.selectedMembers);
      }
  },
