            debugger
            console.log(name);
            var vm = this;
            vm.groupShow = true
            let obj = {
                companyId: vm.sender.companyId,
                userId: vm.sender.id,
                emaiId: vm.sender.emaiId,
                name: vm.sender.name,
                imgPath: vm.store.getBlobUserUrl + vm.sender.imgPath,
                type: vm.type
            }
            debugger
            var params = new Object();
            params = obj;
            params.room = vm.chatRoom.GROUP_NAME_ALIAS
            params.alias =vm.chatRoom.GROUP_NAME_ALIAS
            params.groupName = vm.chatRoom.GROUP_NAME
            params.userIds = vm.chatRoom.GROUP_NAME_ALIAS.replace(/_/g,',')
            // let isGroupOrNot = (params.userIds.split("_").length - 1);
            // if (isGroupOrNot > 1) {
            //     isGroupOrNot = 1;
            // } else {
            //     isGroupOrNot = 0;
            // }
            params.groupId = vm.chatRoom.GROUP_ID
